





























































































import 'reflect-metadata'
import { Vue, Component, Prop } from 'vue-property-decorator'
import CopyText from '@/components/misc/CopyText.vue'
import BalanceTable from '@/components/Address/BalanceTable.vue'
import DJTXBalanceTableSummary from '@/components/Address/DJTXBalanceTableSummary.vue'
import DJTXBalanceTableDetail from '@/components/Address/DJTXBalanceTableDetail.vue'
import { IAddress, IBalanceX } from '@/services/addresses/models'
import Big from 'big.js'
import { DJTX_ID } from '@/known_assets'

@Component({
    components: {
        CopyText,
        BalanceTable,
        DJTXBalanceTableSummary,
        DJTXBalanceTableDetail,
    },
})
export default class Metadata extends Vue {
    @Prop() metaData!: IAddress
    @Prop() addressID!: string
    @Prop() alias!: string
    @Prop() assets!: IBalanceX[]
    @Prop() prefix!: string

    get DJTX(): string {
        return DJTX_ID
    }

    get assetsMap(): any {
        return this.$store.state.assets
    }

    // P-Chain DJTX balance
    get P_unlocked(): Big {
        return this.metaData.P_unlocked
    }

    get P_lockedStakeable(): Big {
        return this.metaData.P_lockedStakeable
    }

    get P_lockedNotStakeable(): Big {
        return this.metaData.P_lockedNotStakeable
    }

    get P_staked(): Big {
        return this.metaData.P_staked
    }

    // X -> P shared memory
    get XP_unlocked(): Big {
        return this.metaData.XP_unlocked
    }

    // X-Chain DJTX balance
    get X_unlocked(): Big {
        return this.metaData.X_unlocked
    }

    get X_locked(): Big {
        return this.metaData.X_locked
    }

    // X -> C shared memory
    get XC_unlocked(): Big {
        return this.metaData.XC_unlocked
    }

    // C-Chain DJTX balance
    // @dev we do not support EVM balances here. only bech32, no 0x
}
