
















































import 'reflect-metadata'
import { Vue, Component, Prop } from 'vue-property-decorator'
import { backgroundColor } from '@/helper'
import Big from 'big.js'

@Component({
    components: {},
})
export default class DJTXBalanceTableDetail extends Vue {
    @Prop() pUnlocked!: Big
    @Prop() pLockedStakeable!: Big
    @Prop() pLockedNotStakeable!: Big
    @Prop() pStaked!: Big
    @Prop() xpUnlocked!: Big
    @Prop() xUnlocked!: Big
    @Prop() xLocked!: Big
    @Prop() xcUnlocked!: Big

    balances: any[] = [
        {
            name: 'Unlocked',
            chain: 'Method',
            balance: this.pUnlocked,
        },
        {
            name: 'Stakeable',
            chain: 'Method',
            balance: this.pLockedStakeable,
        },
        {
            name: 'Not-Stakeable',
            chain: 'Method',
            balance: this.pLockedNotStakeable,
        },
        {
            name: 'Staked',
            chain: 'Method',
            balance: this.pStaked,
        },
        {
            name: 'Unlocked',
            chain: 'Value',
            balance: this.xUnlocked,
        },
        {
            name: 'Locked',
            chain: 'Value',
            balance: this.xLocked,
        },
        {
            name: 'Unlocked',
            chain: 'VU Shared',
            balance: this.xcUnlocked,
        },
        {
            name: 'Total Balance',
            chain: '',
            balance: this.totalDJTX,
        },
    ]

    get totalDJTX(): Big {
        return this.pUnlocked
            .plus(this.pLockedStakeable)
            .plus(this.pLockedNotStakeable)
            .plus(this.xpUnlocked)
            .plus(this.xUnlocked)
            .plus(this.xLocked)
            .plus(this.xcUnlocked)
    }

    background(chain: string): string {
        switch (chain) {
            case 'Method':
                return backgroundColor('M')
            case 'VM Shared':
                return backgroundColor('M')
            case 'Value':
                return backgroundColor('V')
            case 'VU Shared':
                return backgroundColor('U')
            case 'Utility':
                return backgroundColor('U')
            default:
                return 'transparent'
        }
    }
}
