























import 'reflect-metadata'
import { Vue, Component, Prop } from 'vue-property-decorator'
import Big from 'big.js'

@Component({
    components: {},
})
export default class DJTXBalanceTableSummary extends Vue {
    balances: any[] = [
        {
            name: 'Locked',
            balance: this.totalDJTXLocked,
        },
        {
            name: 'Unlocked',
            balance: this.totalDJTXUnlocked,
        },
        {
            name: 'Staked',
            balance: this.totalDJTXStaked,
        },
        {
            name: 'Total Balance',
            balance: this.totalDJTX,
        },
    ]

    @Prop() pUnlocked!: Big
    @Prop() pLockedStakeable!: Big
    @Prop() pLockedNotStakeable!: Big
    @Prop() pStaked!: Big
    @Prop() xpUnlocked!: Big
    @Prop() xUnlocked!: Big
    @Prop() xLocked!: Big
    @Prop() xcUnlocked!: Big

    get totalDJTXStaked(): Big {
        return this.pStaked
    }

    get totalDJTXLocked(): Big {
        return this.pLockedStakeable
            .plus(this.pLockedNotStakeable)
            .plus(this.xLocked)
    }

    get totalDJTXUnlocked(): Big {
        return this.pUnlocked.plus(this.xUnlocked).plus(this.xcUnlocked)
    }

    get totalDJTX(): Big {
        return this.pUnlocked
            .plus(this.pLockedStakeable)
            .plus(this.pLockedNotStakeable)
            .plus(this.xpUnlocked)
            .plus(this.xUnlocked)
            .plus(this.xLocked)
            .plus(this.xcUnlocked)
    }
}
